<template>
  <div id="app">
    <div class="flex mb-10">
      <h3 class="text-grey flex-auto">TOTAL SALES</h3>
      <div class="flex flex-col">
        <h3>$45,340</h3>
        <p class="text-left">USD</p>
      </div>
    </div>
    <canvas id="planet-chart"></canvas>

    <p class="mt-10">Showing last 30 days</p>
  </div>
</template>

<script>
import Chart from "chart.js";
import planetChartData from "./chart-data.js";

export default {
  name: "app",

  data() {
    return {
      planetChartData: planetChartData
    };
  },

  mounted() {
    this.createChart("planet-chart", this.planetChartData);
  },

  methods: {
    createChart(chartId, chartData) {
      const ctx = document.getElementById(chartId).getContext("2d");

      var gradient = ctx.createLinearGradient(0, 0, 0, 450);

      gradient.addColorStop(0, "rgba(131, 66, 244, 0.1)");
      gradient.addColorStop(0.5, "rgba(131, 66, 244, 0.03)");
      gradient.addColorStop(1, "rgba(131, 66, 244, 0)");

      chartData.data.datasets[0].backgroundColor = gradient;

      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options
      });
    }
  }
};
</script>



